// Mes compétences


.icons_container {
    height: 40vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.icons_container>div {
    height: 40vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}

/* Icone individuel */

.iconBloc {
    width: 90px;
}

.bgIcon {
    background-color: rgba(0, 0, 0, 0.87);
    border: solid rgba(255, 255, 255, 0.575) 2px;
    border-radius: 10%;
    font-size: 48px;
}

.iconAnimation {
    transition: all 400ms;
    background-color: rgba(255, 255, 255, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid rgba(0, 0, 0, 0.651);
    border-radius: 10px;
}

.iconAnimation:hover {
    transform: scale(1.50);
    background-color: rgba(255, 255, 255, 0.541);
}



.htmlIcon {
    color: rgb(75, 9, 255);
}

.cssIcon {
    color: rgb(255, 179, 38);
}

.jsIcon {
    color: rgb(255, 255, 82);
}

.phpIcon {
    color: rgb(16, 163, 163);
}

.reactIcon {
    color: rgb(150, 150, 253);
}

.angularIcon {
    color: rgb(255, 64, 64);
}

.sassIcon {
    color: rgb(250, 8, 157);
}

.bsIcon {
    color: rgb(160, 31, 160);
}

.wpIcon {
    color: rgb(104, 104, 253);
}

.apiIcon {
    color: rgb(197, 196, 196);
}

.icons_container>div>div:hover div.tooltip {
    display: block;
}

.tooltip {
    display: none;
}