.img_project {
    width: 80%;
    margin: auto;
    object-fit: cover;
}

.book_container {
    border: solid 1px rgba(0, 0, 0, 0.822);
    border-radius: 10px;
    box-shadow: 0px 10px rgba(0, 0, 0, .15);
    width: 80%;
}



.portefolio_article {
    background-color: rgba(0, 0, 0, .15);
    border: solid 1px rgba(2, 2, 2, 0.979);
    color: white;
    border-radius: 5px;
}

.carousel_portfolio {
    width: 100%;
}

.imgPortFolio {
    width: 95%;
    margin: 1vh auto;
    height: 30vh;
    object-fit: cover;
    object-position: 50% 0%;
    border: 1px solid rgba(3, 3, 1, 0.637);
    border-radius: 10px;
}

.visitButton {
    background: linear-gradient(90deg, rgb(159, 255, 180) 0%, rgba(23, 255, 43, 0.658) 100%);
    color: black;
    padding: 0 5vw;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 5px;
    transition: 500ms;
    font-size: 18px;
}

.visitButton:hover {
    background: linear-gradient(90deg, rgb(201, 255, 213) 50%, rgba(115, 255, 127, 0.79) 100%);
    color: rgba(0, 0, 0, 0.897);
    padding: 0 6vw;
}


@media (max-width: 576px) {
    .book_container {
        width: 90%;
    }
}